import React, { useEffect} from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {Link} from 'react-router-dom'
import{ Row, Col, Image, Form, Button, Card, ListGroup} from 'react-bootstrap'
import {addToCart, removeFromCart} from '../actions/cartActions'
import {toast} from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'

const CartScreen = ({ match, location, history}) => {
    const productId = match.params.id

    const qty = location.search ? Number(location.search.split('=')[1]) : 1

    const dispatch = useDispatch()

    const cart = useSelector(state => state.cart)
    const {cartItems} = cart
    
    console.log(cartItems)

    useEffect(() => {
        if(productId) {
            dispatch(addToCart(productId, qty))
        }
    }, [dispatch, productId, qty])

    const removeFromCartHandler = (id) => {
        dispatch(removeFromCart(id))
        toast('Removed from Cart',{ autoClose: 2000 })
    }
    
    return (
        <Row>
            <Col md={8}>
                <h1>Shopping Cart</h1>
                {cartItems.length===0 ? (<h3>Your Cart is Empty  <Link to ='/' >Go Back</Link></h3>) : (
                    <ListGroup variant='flush'>
                        {cartItems.map(item => (
                            <ListGroup.Item key={item.product}>
                                <Row>
                                    <Col md={2}>
                                        <Image src ={item.image} alt = {item.name} fluid rounded/> 
                                    </Col>
                                    <Col md={3}>
                                        <Link to = {`/product/${item.product}`}>{item.name}</Link>
                                    </Col>
                                    <Col md={2}>Rs {item.price}</Col>
                                    <Col md ={2}>
                                    <Form.Control as='select' value={item.qty} onChange={(e) => dispatch(addToCart(item.product, Number(e.target.value)))}>
                                        {[...Array(item.countInStock).keys()].map(x=> (
                                            <option key={x+1} value ={x+1}>{x+1}</option>
                                        ))}
                                    </Form.Control>
                                    </Col>
                                    <Col md ={2}>
                                        <Button type='button' variant='light' onClick={() => removeFromCartHandler(item.product)}><i class="fas fa-trash"></i></Button>
                                    </Col>
                                </Row>
                            </ListGroup.Item>
                        ))}
                    </ListGroup>
                )}
            </Col>
            <Col md={4}>
                <Card>
                    <ListGroup variant='flush'>
                        <ListGroup.Item>
                            <h2>Subtotal ({cartItems.reduce((acc,item) => acc+item.qty, 0)}) items</h2>
                            Rs {cartItems.reduce((acc,item) => acc+item.qty*item.price, 0)}
                        </ListGroup.Item>
                        <ListGroup.Item>
                            <Button type='button' className='btn-block' disabled={cartItems.length === 0}>Proceed To CheckOut</Button> 
                        </ListGroup.Item>
                    </ListGroup>
                </Card>
            </Col>
        </Row>
    )
}

export default CartScreen
